.ClassList {
  color: #6e92eb;
  padding: 1em 0;
}

.ClassList >li {
  display: inline-block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 2px ;
  padding: 10px;
  border: 1px solid;
  cursor: pointer;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
}

.ClassList li:hover {
  color: #3b69d8;
  font-weight: bold;
  cursor: pointer;
}


