.PlayDetail {
  color: #6e92eb
}
.PlayTime {
  padding: 0 1em .5em;
  overflow: hidden;
  color: #dee4f6;
}
.PlayTime .current {
  float: left;
}

.PlayTime .duration {
  float: right;
}

.durationBar {
  height: 6px;
  width: 100%;
  margin-bottom: 20px;
  background-color: #dee4f6;
  border-radius: 3px;
  cursor: pointer;
}

.progressBar {
  height: 6px;
  background-image: -webkit-gradient(linear, left top, right top, from(#6e92eb), to(#3b69d8));
  background-image: -webkit-linear-gradient(left, #6e92eb, #3b69d8);
  background-image: -o-linear-gradient(left, #6e92eb, #3b69d8);
  background-image: linear-gradient(90deg, #6e92eb, #3b69d8);
  border-radius: 3px;
}

.PlayDetail .container {
  height: 3rem;
  overflow: hidden;
  margin-bottom: 1rem;
}

.PlayDetail .lyric {
  position: relative;
  -webkit-transition: top 1s;
  -o-transition: top 1s;
  transition: top 1s;
}
.PlayDetail .lyric li {
  font-size: 1rem;
  line-height: 1.5rem;
  min-height: 1.5rem;
}

.PlayDetail .lyric li.target {
  font-size: 1.2rem;
  color: #3b59d8;
  font-weight: bold;
}

.container.height15Rem  {
  margin-top: 1rem;
  height: 15rem;
}



